/* Font imports */



/* Global CSS Variables */
:root {
  --color-main: #BFAB86;
  --color-main-2: #1B1D21;
  --on-hero: #ffffff;
  --shadow-soft: 0 10px 30px rgba(0,0,0,.18);

  /* 195px on desktop, shrink on small screens */
  --hero-radius: clamp(80px, 13vw, 195px);

  /* Typography that matches Figma */
  --hero-title-size: clamp(32px, 3.6vw, 48px);
  --hero-sub-size: clamp(16px, 1.7vw, 22px);

  --hero-side-padding: clamp(20px, 3vw, 40px);
  --hero-vertical-pad: clamp(30px, 6vh, 80px);

  --cta-idle-bg: var(--color-main);
  --cta-active-bg: var(--color-main-2);
  --cta-hover-bg: #B68A35;
  --radius-lg: 14px;
  --t-fast: 180ms cubic-bezier(.2,.6,.2,1);
  --t-slow: 420ms cubic-bezier(.2,.6,.2,1);

  /* Sidebar variables */
  --sidebar-w: 272px;
  --sidebar-w-collapsed: 80px;
  --sidebar-bg: #F3F8F4;
  --text-primary: #333;
  --text-secondary: #666;
  --border-color: #E5E7EB;
  --shadow-light: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  
  /* Brand colors */
  --brand-green: #BFAB86;
  --brand-blue: #1B1D21;
}

/* Global styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: 'IBM Plex Sans Arabic', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
}

/* Global Font Rules */
/* English text - Poppins */
[dir="ltr"] *,
.ltr * {
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Arabic text - IBM Plex Sans Arabic */
[dir="rtl"] *,
.rtl * {
  font-family: 'IBM Plex Sans Arabic', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Specific font overrides for better control */
[dir="ltr"] h1, [dir="ltr"] h2, [dir="ltr"] h3, [dir="ltr"] h4, [dir="ltr"] h5, [dir="ltr"] h6,
.ltr h1, .ltr h2, .ltr h3, .ltr h4, .ltr h5, .ltr h6 {
  font-family: 'Poppins', sans-serif;
}

[dir="rtl"] h1, [dir="rtl"] h2, [dir="rtl"] h3, [dir="rtl"] h4, [dir="rtl"] h5, [dir="rtl"] h6,
.rtl h1, .rtl h2, .rtl h3, .rtl h4, .rtl h5, .rtl h6 {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

[dir="ltr"] p, [dir="ltr"] span, [dir="ltr"] div, [dir="ltr"] label, [dir="ltr"] input, [dir="ltr"] button,
.ltr p, .ltr span, .ltr div, .ltr label, .ltr input, .ltr button {
  font-family: 'Poppins', sans-serif;
}

[dir="rtl"] p, [dir="rtl"] span, [dir="rtl"] div, [dir="rtl"] label, [dir="rtl"] input, [dir="rtl"] button,
.rtl p, .rtl span, .rtl div, .rtl label, .rtl input, .rtl button {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

/* RTL/LTR Support */
.rtl {
  direction: rtl;
  text-align: right;
}

.ltr {
  direction: ltr;
  text-align: left;
}

/* Button styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  border: none;
  border-radius: var(--radius-lg);
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: background-color var(--t-slow), color var(--t-slow), box-shadow var(--t-slow), border-color var(--t-slow), opacity var(--t-slow), visibility var(--t-slow), height var(--t-slow), max-height var(--t-slow);
  min-height: 48px;
  white-space: nowrap;
}

.btn-primary {
  background-color: #BFAB86;
  color: white;
}

.btn-primary:hover {
  background-color: #B8941F;
}

.btn-primary:active {
  background-color: #B8941F;
}

.btn-secondary {
  background-color: transparent;
  color: var(--color-main-2);
  border: 2px solid var(--color-main-2);
}

.btn-secondary:hover {
  background-color: var(--color-main-2);
  color: white;
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--color-main-2);
}

.form-control {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: var(--radius-lg);
  font-size: 16px;
  transition: border-color var(--t-fast);
  min-height: 48px;
}

.form-control:focus {
  outline: none;
  border-color: var(--color-main);
}

.form-control.error {
  border-color: #dc3545;
}

.form-error {
  color: #dc3545;
  font-size: 14px;
  margin-top: 4px;
}

/* Card styles */
.card {
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-soft);
  padding: 32px;
  border: 1px solid #e1e5e9;
}

/* Utility classes */
.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.mb-1 { margin-bottom: 8px; }
.mb-2 { margin-bottom: 16px; }
.mb-3 { margin-bottom: 24px; }
.mb-4 { margin-bottom: 32px; }

.mt-1 { margin-top: 8px; }
.mt-2 { margin-top: 16px; }
.mt-3 { margin-top: 24px; }
.mt-4 { margin-top: 32px; }

/* RTL specific adjustments */
.rtl .text-left {
  text-align: right;
}

.rtl .text-right {
  text-align: left;
}

/* Responsive design */
@media (max-width: 768px) {
  .card {
    padding: 24px;
  }
  
  .btn {
    padding: 10px 20px;
    min-height: 44px;
  }
  
  .form-control {
    min-height: 44px;
  }
}

/* RTL Modal Adjustments */
html[dir="rtl"] .modal-header {
  flex-direction: row-reverse;
}

html[dir="rtl"] .modal-header .btn-close {
  order: 1;
}

html[dir="rtl"] .modal-title {
  order: 2;
  text-align: right;
  font-family: 'IBM Plex Sans Arabic', sans-serif !important;
}

html[dir="rtl"] .modal-footer {
  flex-direction: row-reverse;
}

/* Ensure modal titles use correct fonts in both languages */
html[dir="ltr"] .modal-title {
  font-family: 'Poppins', sans-serif !important;
}

html[dir="rtl"] .modal-title {
  font-family: 'IBM Plex Sans Arabic', sans-serif !important;
}

/* Global modal header styling */


.modal-header .modal-title,
.modal-header h2,
.modal-header h3 {
  color: white !important;
}

.modal-header .btn-close,
.modal-header .close-btn {
  color: white !important;
}

.modal-header .btn-close:hover,
.modal-header .close-btn:hover {
  background-color: rgba(255, 255, 255, 0.1) !important;
  color: white !important;
}

/* Custom Scrollbar Styles */
/* Webkit browsers (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #F9F7ED;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #BFAB86;
  border-radius: 4px;
  transition: background-color var(--t-fast);
}

::-webkit-scrollbar-thumb:hover {
  background: #92722A;
}

::-webkit-scrollbar-thumb:active {
  background: var(--cta-active-bg);
}

::-webkit-scrollbar-corner {
  background: #F9F7ED;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #BFAB86 #F9F7ED;
}

/* Custom scrollbar for specific containers */
.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #BFAB86 #F9F7ED;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #F9F7ED;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #BFAB86;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #92722A;
}

/* Sidebar specific scrollbar - very small */
.sidebar-nav.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #BFAB86 transparent;
}

.sidebar-nav.custom-scrollbar::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}

.sidebar-nav.custom-scrollbar::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 2px;
}

.sidebar-nav.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #BFAB86;
  border-radius: 2px;
  opacity: 0.7;
}

.sidebar-nav.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #92722A;
  opacity: 1;
}

/* Dark scrollbar variant for dark backgrounds */
.dark-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: var(--brand-green) rgba(255, 255, 255, 0.1);
}

.dark-scrollbar::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.dark-scrollbar::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.dark-scrollbar::-webkit-scrollbar-thumb {
  background: var(--brand-green);
  border-radius: 4px;
}

.dark-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #ffffff;
}

/* RTL scrollbar adjustments */
[dir="rtl"] ::-webkit-scrollbar {
  direction: rtl;
}

[dir="rtl"] .custom-scrollbar::-webkit-scrollbar {
  direction: rtl;
}

/* Global RTL Dropdown Arrow Alignment */
[dir="rtl"] .form-select {
  background-position: left 0.75rem center;
  padding-left: 2.25rem;
  padding-right: 0.75rem;
}

[dir="ltr"] .form-select {
  background-position: right 0.75rem center;
  padding-right: 2.25rem;
  padding-left: 0.75rem;
}

/* Ensure dropdown arrows don't overlap with text */
[dir="rtl"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

[dir="ltr"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

/* Focus state for RTL dropdowns */
[dir="rtl"] .form-select:focus {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select:focus {
  background-position: right 0.75rem center;
}

/* Invalid state for RTL dropdowns */
[dir="rtl"] .form-select.is-invalid {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select.is-invalid {
  background-position: right 0.75rem center;
}

/* Ensure proper text alignment in dropdowns */
[dir="rtl"] .form-select {
  text-align: right;
}

[dir="ltr"] .form-select {
  text-align: left;
}

/* Global RTL Support for Modal Headers */
.modal-header[dir="rtl"],
.rtl-header {
  flex-direction: row-reverse !important;
  justify-content: flex-end !important;
  display: flex !important;
  align-items: center !important;
}

.modal-header[dir="ltr"],
.ltr-header {
  flex-direction: row !important;
  justify-content: flex-start !important;
  display: flex !important;
  align-items: center !important;
}

.modal-header[dir="rtl"] .modal-title,
.rtl-title {
  text-align: right !important;
  margin-left: auto !important;
  margin-right: 0 !important;
  flex: 1 !important;
}

.modal-header[dir="ltr"] .modal-title,
.ltr-title {
  text-align: left !important;
  margin-left: 0 !important;
  margin-right: auto !important;
  flex: 1 !important;
}

/* Force RTL text alignment for Arabic modals */
[dir="rtl"] .modal-title {
  text-align: right !important;
}

[dir="ltr"] .modal-title {
  text-align: left !important;
}

/* Additional RTL classes for better control */
.rtl-header .rtl-title {
  text-align: right !important;
  margin-left: auto !important;
  margin-right: 0 !important;
  flex: 1 !important;
}

.ltr-header .ltr-title {
  text-align: left !important;
  margin-left: 0 !important;
  margin-right: auto !important;
  flex: 1 !important;
}